<template>
  <keep-alive :include="cachedViews">
    <router-view :key="$route.fullPath"></router-view>
  </keep-alive>
</template>
<script>
export default {
  name: 'MiddleComponent',
  computed: {
    cachedViews() {
      return this.$store.state.tagsView.cachedViews;
    }
  },
  mounted() {
    this.$store.dispatch('tagsView/addCachedView', this.$route.matched[1]);
  }
};
</script>
